.formContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 50px;
  align-items: center;
}

.form {
  display: flex;
  width: 150px;
  position: relative;
  transition: all 0.5s ease;
}

.input {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
}

.form:focus-within {
  width: 300px;
}

.icon {
  transition: color 0.3s ease;
}
.form:focus-within .icon {
  color: var(--main-color);
}

.searchButton {
  position: absolute;
  top: 5px;
  right: -5px;
  border: none;
  background-color: inherit;
  align-items: center;
}
